.sidebar ul, li {
  padding: 0;
  margin: 0;
  list-style: none; }

a {
  text-decoration: none; }

@font-face {
  font-family: 'fontello';
  src: url("/font/mainfont/fontello.ttf");
  font-weight: normal;
  font-style: normal; }
html, body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font: 100 16px/1.5  "Microsoft Yahei","Hiragino Sans GB",Helvetica,"Helvetica Neue",Tahoma,Arial,sans-serifl; }

body {
  position: relative;
  display: none; }

h1 {
  text-align: center; }

h2 {
  font-weight: normal; }

.pc .wrapper {
  height: 100%; }
.pc .sidebar {
  position: relative;
  height: 100%;
  width: 170px;
  float: left;
  transition: margin-left 0.5s;
  z-index: 1;
  display: flex;
  flex-direction: column; }
.pc .avatrbox {
  background: #323950;
  flex: 0 0 184px; }
.pc .avatrwrapper {
  overflow: hidden; }
.pc .avatar {
  display: block;
  margin: 40px auto;
  width: 100px;
  height: 100px;
  box-shadow: 0 0 2px 4px white;
  border: 2px solid white;
  border-radius: 50%;
  overflow: hidden; }
.pc .avatar img {
  width: 100px;
  height: 100px; }
.pc #article-category {
  background: #323950;
  box-shadow: -4px 0 8px #323950 inset;
  flex: 1 1 0%; }
.pc .category {
  height: 48px;
  border-top: 1px solid #3B435A;
  line-height: 48px;
  text-align: center;
  color: #EAE4E4; }
.pc .category:nth-last-child(1) {
  border-bottom: 1px solid #3B435A; }
.pc .category:hover {
  color: white;
  cursor: pointer;
  background: #232A40; }
.pc .post-list {
  position: absolute;
  top: 0;
  left: -209px;
  height: 100%;
  width: 379px;
  color: white;
  overflow-y: auto;
  background: #232A40;
  z-index: -1;
  box-shadow: inset -10px 0 10px #232A40;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px; }
.pc .post-list:hover {
  left: 170px; }
.pc .post-list a {
  display: block;
  padding: 5px 15px;
  color: white;
  text-shadow: 0px 0px 0.1px white;
  text-align: left; }
.pc .sidebar li:hover .post-list {
  left: 170px;
  transition: left 0.5s, opacity 0.5s; }
.pc #post {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  background: url(/blog/images/1.jpg);
  background-size: 100% 100%; }
.pc .post-content {
  position: relative;
  overflow: hidden;
  margin: 20px auto;
  box-sizing: border-box;
  transition: border 2s;
  max-width: 800px;
  width: 80%;
  color: #2c353d;
  background: white;
  opacity: 0.9;
  box-shadow: 0 0 20px black;
  padding: 0 20px 10px 20px; }
.pc .post-content a {
  color: #7d8b99; }
.pc ::-webkit-scrollbar {
  width: 2px;
  height: 2px;
  background-color: transparent; }
.pc ::-webkit-scrollbar-thumb {
  border-radius: 1px;
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.3);
  background-color: transparent; }
.pc .contentsizeicon {
  cursor: pointer;
  color: #fff;
  font-weight: normal; }
.pc .hidesidebar {
  margin-left: -170px; }
.pc .closebtn {
  flex: 0 0 36px;
  background: #323950;
  display: flex;
  justify-content: flex-end; }
.pc .closebtn i {
  margin-right: 10px;
  margin-top: 10px; }
.pc .fullnav {
  opacity: 0;
  transform: translateX(20px);
  position: absolute;
  top: 12px;
  left: 25px; }
.pc .fullnavafterclick {
  opacity: 1;
  transform: translateX(0);
  transition: transform 0.5s 0.3s, opacity 0.5s 0.3s;
  cursor: pointer;
  z-index: 1;
  position: absolute;
  top: 12px;
  left: 25px;
  border: 1px solid #323950;
  color: #323950; }
.pc .fullnavafterclick:hover {
  color: white;
  background: #323950;
  text-shadow: 0 0 2px; }

body.mobile {
  height: 100%;
  background: url(/blog/images/1.jpg) top left no-repeat;
  background-size: 100% 100%;
  position: relative; }

.mobile .wrapper {
  height: 100%;
  overflow: scroll;
  transition: transform 0.5s cubic-bezier(0.11, 1.01, 1, 1); }
.mobile .sidebar {
  height: 194px;
  transition: margin-top 0.5s cubic-bezier(0.11, 1.01, 1, 1);
  overflow: auto; }
.mobile .avatrbox {
  background: #323950; }
.mobile .avatrwrapper {
  overflow: hidden;
  margin: 0 48px; }
.mobile .avatar {
  display: block;
  width: 100px;
  height: 100px;
  margin: 20px auto;
  box-shadow: 0 0 2px 4px white;
  border: 2px solid white;
  border-radius: 50%;
  overflow: hidden; }
.mobile .avatar img {
  width: 100px;
  height: 100px; }
.mobile #article-category {
  width: 100%;
  height: 50px;
  background: #323950; }
.mobile #article-category ul {
  overflow: auto; }
.mobile .category {
  height: 49px;
  color: #EAE4E4;
  line-height: 48px;
  text-align: center;
  border-top: 1px solid #3B435A;
  width: 25%;
  float: left;
  list-style: none; }
.mobile .category:hover {
  color: white;
  cursor: pointer;
  background: #232A40; }
.mobile li.active:hover {
  background: #fff;
  color: black; }
.mobile .post-list {
  position: absolute;
  top: 194px;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: auto;
  overflow-y: auto;
  display: none;
  z-index: 1;
  background: #fff; }
.mobile .post-list a {
  display: block;
  padding: 5px 15px;
  color: black;
  text-shadow: 0px 0px 0.1px white;
  text-align: left; }
.mobile #post {
  flex: 1 1 0;
  overflow-y: auto; }
.mobile .post-content {
  position: relative;
  overflow: hidden;
  margin: 40px auto 5px;
  box-sizing: border-box;
  transition: border 2s;
  max-width: 800px;
  width: 80%;
  color: #2c353d;
