  opacity: 0.9;
  box-shadow: 0 0 20px black;
  padding: 0 20px 10px 20px; }
.mobile .post-content a {
  color: #7d8b99; }
.mobile .active {
  background: #fff;
  color: black; }
.mobile .contentsizeicon {
  cursor: pointer;
  color: #fff; }
.mobile .hidesidebar {
  /* transform: translateY(-194px); */
  margin-top: -194px; }
.mobile .fullnav {
  opacity: 0;
  transform: translateY(8px);
  position: absolute;
  top: 12px;
  right: 12px; }
.mobile .fullnavafterclick {
  opacity: 1;
  transform: translateY(0);
  transition: transform 0.5s 0.3s, opacity 0.5s 0.3s;
  cursor: pointer;
  z-index: 1;
  position: absolute;
  top: 12px;
  right: 12px;
  border: 1px solid #323950;
  color: #323950; }
.mobile .fullnavafterclick:hover {
  color: white;
  background: #323950;
  text-shadow: 0 0 2px; }
.mobile ::-webkit-scrollbar {
  width: 2px;
  height: 2px;
  background-color: transparent; }
.mobile ::-webkit-scrollbar-thumb {
  border-radius: 1px;
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.3);
  background-color: transparent; }
.mobile .closebtn {
  float: right;
  margin: 8px 12px; }
